.grid-area {
    margin-top: 50px;
    table {
        max-width: 700px;
        margin: auto;
        user-select: none;
        td {
            width: 25px;
            height: 25px;
            &.selected {
                color: rgb(255, 106, 0);
            }
        }
    }
}